<template>
  <div id="painter">
    <v-container>
      <v-row>
        <canvas 
          id="jsCanvas" 
          class="canvas" 
          @mousemove="OnMouseMove"
          @mousedown="OnMouseDown"
          @mouseup="OnMouseUp"
          @mouseleave="onMouseLeave"
          @contextmenu="(e)=>e.preventDefault()"></canvas>
      </v-row>
      <v-row>
        <v-btn small color="indigo" @click="btnSaveClick">다운로드</v-btn>
        <v-btn small color="indigo" @click="btnClearClick">모두 지우기</v-btn>
      </v-row>
      <v-row>
        <div class="controls">
          <div class="controls__colors" id="jsColors">
            <div class="controls__color" @click="changeColor" style="background-color:#2c2c2c"></div>
            <div class="controls__color" @click="changeColor" style="background-color:white"></div>
            <div class="controls__color" @click="changeColor" style="background-color:#FF3B30"></div>
            <div class="controls__color" @click="changeColor" style="background-color:#FF9500"></div>
            <div class="controls__color" @click="changeColor" style="background-color:#FFCC00"></div>
            <div class="controls__color" @click="changeColor" style="background-color:#4cd963"></div>
            <div class="controls__color" @click="changeColor" style="background-color:#5ac8fa"></div>
            <div class="controls__color" @click="changeColor" style="background-color:#0579FF"></div>
            <div class="controls__color" @click="changeColor" style="background-color:#5856d6"></div>
          </div>
        </div>
      </v-row>
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'Painter',
  data(){
    return{
      width: 400,
      height: 400,
      painting: false,
      strokeStyle: '#2c2c2c',
      lineWidth: 2.5,
      ctx: null,
    }
  },
  methods:{
    OnMouseMove : function(e) {
      const x = e.offsetX;
      const y = e.offsetY;
      if(!this.painting){
        this.ctx.beginPath();
        this.ctx.moveTo(x, y);
      }else{
        this.ctx.lineTo(x, y);
        this.ctx.stroke();
      }
    },
    OnMouseDown : function() {
      this.painting = true;
    },
    OnMouseUp : function() {
      this.painting = false;
    },
    onMouseLeave : function() {
      this.painting = false;
    },
    changeColor : function(e) {
      this.ctx.strokeStyle = e.target.style.backgroundColor;
    },
    btnSaveClick : function() {
      const image = document.getElementById('jsCanvas').toDataURL();
      const link = document.createElement("a");
      link.href = image;
      link.download = "paintImg";
      link.click();
    },
    btnClearClick : function() {
      this.ctx.clearRect(0, 0, this.width, this.height);
    }
  },
  mounted(){
    // Canvas init
    const canvas = document.getElementById('jsCanvas');
    this.ctx = canvas.getContext('2d');
    canvas.width = this.width;
    canvas.height = this.height; 
    this.ctx.strokeStyle = this.strokeStyle; // 기본은 검정색
    this.ctx.lineWidth = this.lineWidth; // 기본은 2.5
  }
}
</script>

<style scoped>
  .canvas{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 400px;
    height: 400px;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0 4px 6px rgba(50, 50, 93, 0.11), 0 1px 3px rgba(0, 0, 0, 0.8);
  }
  .controls{
    display: block;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
  }
  .controls_.controls__colors{
    display: flex;
  }
  .controls__colors .controls__color{
    width: 40px;
    height: 40px;
    float: left;
    margin-right: .3rem;
    border-radius: 25px;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(50, 50, 93, 0.11), 0 1px 3px rgba(0, 0, 0, 0.8);
  }
</style>